.header {
    // START UPPER NAV 
    .upper-navbar {
        background-color: $colorYellow;
        padding: 7px;
        .upper-navbar--postion {
            display: flex;
            justify-content: space-between;
            align-items: center; 
        }
        .upper-navbar__info {
            .upper-navbar__info__link {
                font-size: calc(14px + ((20 - 14)*((100vw - 1366px)/(1920-1366))));
                font-family: $interFont;
                font-weight: 500;
                line-height: 1.21;
                margin-#{$end}: 50px;
                color: #212529;
            }
        }
        .upper-navbar__social {
            .upper-navbar__social__link {
                margin-#{$start}: 8px;
                font-size: 23px;
            }
        }
    }
    // END UPPER NAV 
    .main-navbar {
        background-color: $colorBackground;
        font-family: $interFont;
        padding-bottom: 80px;
        padding-top: 17px;
        .navbar-toggler {
            border: 0;
        }
        .navbar-toggler:focus {
            box-shadow: none;
        }
        .navbar-toggler[aria-expanded='true'] {
            .navbar-toggler-icon {
                background-image: url(../images/times-solid.svg);
                
            }

        }
        .navbar-toggler.collapsed {
            .icon-close {
                background-image: url(../images/download.svg) !important;
                
            }
        }
            .navbar-brand {
                color: $colorBlue;
                font-family: $interFont;
                font-weight: 500;
                text-transform: uppercase;
                font-size: calc(24px + ((30 - 24)*((100vw - 1366px)/(1920-1366))));
                display: flex;
                align-items: center;
            }

            .navbar-nav {
                .nav-item {
                    .nav-link {
                        font-family: $interFont;
                        color: $colorBlue;
                        line-height: 1.31;
                        font-weight: bold;
                        font-size: calc(16px + ((22 - 16)*((100vw - 1366px)/(1920-1366))));
                        padding-right: 27px;
                    }
                }
            }

            .search-btn {
                background-color: $colorWhite;
                border: 1px solid #bdbdbd;
                padding: 9px 13px;
                padding: 13px;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .sign-up-btn {
                @extend.my-btn;
                padding: 9px 28px;
                margin: 0 15px 0 35px;
            }
        }
}

    @media only screen and (max-width:767px) {
        .header {
            .upper-navbar {
                .upper-navbar--postion {
                    flex-direction: column;
                }

                .upper-navbar__info {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .upper-navbar__info__link {
                        font-size: calc(13px + ((19 - 13)*((100vw - 320px)/(1366-320))));
                        margin: 4px;
                    }
                }

                .upper-navbar__social {
                    text-align: center;
                }
            }

            .main-navbar {
                .navbar-brand {
                    font-size: calc(16px + ((22 - 16)*((100vw - 320px)/(1366 - 320))));
                }
                .navbar-toggler{z-index: 1000;}
                .navbar-nav {
                    .nav-item {
                        .nav-link {
                            padding-left: 20px;
                            margin:5px;
                        }
                    }
                }
            }
        }
    }
    @media only screen and (max-width:992px) {
        .header{
            .main-navbar{
                .navbar-collapse{
                    position: absolute;
                    top: 75px;
                    left: 0;
                    background: #eee;
                    width: 100%;
                    z-index: 1000;
                    padding: 15px;
                }
            }
        }
    }
    @media only screen and (min-width:1366px) {
        .header {
            .main-navbar {
                padding-bottom: 95px;
            }
        }
    }
